/* ======================= MEDIA QUERIES (SMALL DEVICES) ============= */ 
@media screen and (max-width: 600px) {
    header {
        height: 100vh;  /* 100vh */
    }

    .header_socials,
    .scroll_down {
        display: none;
    }
}

